import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {EssaysService} from '../essays.service';
import {Essay} from '../essay';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-essay-view',
  templateUrl: './essay-view.component.html',
  styleUrls: ['./essay-view.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class EssayViewComponent implements OnInit {

  id: string;
  content: string;
  essay: Essay;

  constructor(private route: ActivatedRoute, private essaysService: EssaysService, private http: HttpClient) { }

  ngOnInit() {
    this.id = this.route.snapshot.paramMap.get('id');
    this.essay = this.essaysService.getEssay(this.id);
    this.http.get("./assets/essays/" + this.essay.url, {responseType: 'text'}).subscribe(data => {
      this.content = data;
    });
  }


}
