import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {MediaService} from '../media.service';
import {HttpClient} from '@angular/common/http';
import {Media} from '../media';

@Component({
  selector: 'app-media-view',
  templateUrl: './media-view.component.html',
  styleUrls: ['./media-view.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class MediaViewComponent implements OnInit {

  id: string;
  content: string;
  media: Media;

  constructor(private route: ActivatedRoute, private mediaService: MediaService, private http: HttpClient) {}

  ngOnInit() {
    this.id = this.route.snapshot.paramMap.get('id');
    this.media = this.mediaService.getMedia(this.id);
    this.http.get("./assets/media/" + this.media.article, {responseType: 'text'}).subscribe(data => {
      this.content = data;
    });
  }

}
