import { Injectable } from '@angular/core';
import {Media} from './media';

@Injectable({
  providedIn: 'root'
})
export class MediaService {

  mediaDirectory: Media[];

  constructor() {
    this.mediaDirectory = [];
    this.mediaDirectory.push(new Media("shin-sekai-yori", "Shin Sekai Yori", "television", "assets/photos/ssy.jpg", "shin-sekai-yori.html"));
    // this.mediaDirectory.push(new Media("rottk", "Romance of the Three Kingdoms", "books", "https://i.imgur.com/bNkaVdl.png", "rottk.html"));
    this.mediaDirectory.push(new Media("logh", "Legend of the Galactic Heroes", "television", "assets/photos/logh.jpg", "logh.html"))
  }

  getMediaDirectory(): Media[] {
    return this.mediaDirectory;
  }

  getMedia(id: string): Media {
    for (let i = 0; i < this.mediaDirectory.length; i++) {
      if (this.mediaDirectory[i].id == id) {
        return this.mediaDirectory[i];
      }
    }
    return null;
  }
}
