import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {EssaysService} from '../essays.service';
import {Essay} from '../essay';

@Component({
  selector: 'app-essays',
  templateUrl: './essays.component.html',
  styleUrls: ['./essays.component.css']
})
export class EssaysComponent implements OnInit {

  essays: Essay[];
  sortBy: string;
  order: string;

  constructor(private essaysService: EssaysService) {
    this.sortBy = "date";
    this.order = "descending";
  }

  ngOnInit() {
    this.essays = this.essaysService.getEssayDirectory();
  }

}
