export class Media {
  id: string;
  title: string;
  category: string;
  thumbnail: string;
  article: string;

  constructor(id: string, title: string, category: string, thumbnail: string, article: string) {
    this.id = id;
    this.title = title;
    this.category = category;
    this.thumbnail = thumbnail;
    this.article = article;
  }
}
