import { Injectable } from '@angular/core';
import {Essay} from './essay';

@Injectable({
  providedIn: 'root'
})
export class EssaysService {

  essayDirectory : Essay[];

  constructor() {
    this.essayDirectory = [];
    this.essayDirectory.push(new Essay("collectivism-and-individualism",
      "Collectivism and Individualism, Progress and Regress, Societal Trajectory and the Individual Will in Science Fiction",
      "collectivism-and-individualism-in-science-fiction.html",
      new Date("4/30/19")));
    this.essayDirectory.push(new Essay("logh-rotk-and-rational-fiction-in-general",
      "Opposing Systems of Virtue: Legend of the Galactic Heroes, Romance of the Three Kingdoms, and Rational Fiction",
      "logh-rotk-and-rational-fiction-in-general.html",
      new Date("4/7/18")));
    this.essayDirectory.push(new Essay("tensions-between-rationality-realism-and-humanity",
      "On the Tensions Between Rationality, Realism, and Humanity in HPMOR and Rationalist Fiction",
      "tensions-between-rationality-realism-and-humanity.html",
      new Date("3/31/16")));
  }

  getEssayDirectory(): Essay[] {
    return this.essayDirectory;
  }

  getEssay(id: string) : Essay {
    for (let i = 0; i < this.essayDirectory.length; i++) {
      if (this.essayDirectory[i].id == id) {
        return this.essayDirectory[i];
      }
    }

    return null;
  }

}
